import { FC, useContext, useEffect } from "react";
import classNames from "classnames/bind";

import { Container } from "common/components/Container";
import { SectionWithImage } from "common/components/SectionWithImage";
import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import { Picture } from "common/components/Picture";
import { Text } from "common/components/Text";
import { BookAppointmentFullForm } from "common/components/BookAppointmentFullForm";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { OurResults } from "common/components/new/OurResults";
import { Link } from "common/components/Link";
import { Heading, HeadingTag } from "common/components/new/Heading";
import { ErrorType } from "common/components/ErrorLayout";
import { ErrorContext } from "common/context/errorContext";
import {
    getRefCodeFromQuery,
    getReferralSourceFromQuery,
} from "common/utils/referral";

import { ReferralPageProps } from "./ReferralPageLayout.types";
import styles from "./ReferralPageLayout.module.scss";

const cx = classNames.bind(styles);

export const ReferralPageLayout: FC<ReferralPageProps> = ({
    content,
    version,
    language,
}) => {
    const { main, form, ourResults, terms, contacts } = content;

    const { setErrorType } = useContext(ErrorContext);
    const code = getRefCodeFromQuery();
    const referralSource = getReferralSourceFromQuery();

    useEffect(() => {
        if (!code) {
            setErrorType(ErrorType.REFERRAL);
        }
    }, []);

    return (
        <main className={cx("root")}>
            <Container>
                <section className={cx("main")}>
                    <Link
                        className={cx("main__logo")}
                        {...contacts.site}
                        id={getAnalyticsId({
                            zone: "header",
                            element: GetAnalyticsIdElement.LINK,
                            label: "logo",
                        })}
                    >
                        <Icon>
                            <LogoIcon />
                        </Icon>
                    </Link>

                    <SectionWithImage image={main.image}>
                        <Picture
                            className={cx("main__badge")}
                            {...main.badge}
                        />
                        <Heading
                            className={cx("main__title")}
                            tag={HeadingTag.H2}
                        >
                            {main.title}
                        </Heading>
                        <Text className={cx("main__description")}>
                            <span
                                dangerouslySetInnerHTML={{
                                    __html: main.description,
                                }}
                            />
                        </Text>
                    </SectionWithImage>
                </section>
                <section className={cx("booking")}>
                    <Heading
                        className={cx("booking__title")}
                        tag={HeadingTag.H2}
                    >
                        {form.title}
                    </Heading>
                    <BookAppointmentFullForm
                        className={cx("booking__form")}
                        version={version}
                        language={language}
                        analyticId={getAnalyticsId({
                            zone: "referral-page",
                        })}
                        additionalContactData={{
                            refCode: code,
                        }}
                        additionalProperties={{
                            ...(referralSource && {
                                referral_source: referralSource,
                            }),
                        }}
                    />
                </section>
                <section className={cx("results")}>
                    <OurResults {...ourResults} />
                </section>
                <footer>
                    <Link link={terms.link}>
                        <Text className={cx("terms")}>
                            <span
                                dangerouslySetInnerHTML={{
                                    __html: terms.title,
                                }}
                            />
                        </Text>
                    </Link>
                </footer>
            </Container>
        </main>
    );
};
