import { convertQueryStringToQueryObject } from "common/utils/queryString";

export const getRefCodeFromQuery = () => {
    const hasWindow = typeof window !== "undefined";

    if (hasWindow) {
        const queryObject = convertQueryStringToQueryObject(location.search);

        return queryObject.code;
    }
};

export const getReferralSourceFromQuery = () => {
    const hasWindow = typeof window !== "undefined";

    if (hasWindow) {
        const queryObject = convertQueryStringToQueryObject(location.search);

        return queryObject.referral_source;
    }
};
